.searchBtn {
  box-shadow: 0px 0px 1px 0px #fff6af;
  background: linear-gradient(to bottom, #ffec64 5%, #ffab23 100%);
  background-color: #ffec64;
  border-radius: 9px;
  border: 2px solid #ffaa22;
  display: inline-block;
  cursor: pointer;
  color: #333333;
  font-family: Arial;
  font-size: 12px;
  font-weight: bold;
  font-style: italic;
  padding: 8px 13px;
  text-decoration: none;
  text-shadow: 0px 1px 0px #ffee66;
}

.searchBtn:hover {
  background: linear-gradient(to bottom, #ffab23 5%, #ffec64 100%);
  background-color: #ffab23;
}

.searchBtn:active {
  position: relative;
  top: 1px;
}

.inputStyle {
  padding: 5px;
  font-size: 15px;
  border-width: 2px;
  border-color: #ffe708;
  background-color: #f1eeee;
  color: #000000;
  border-style: outset;
  border-radius: 9px;
  box-shadow: 0px 0px 5px rgba(66, 66, 66, .75);
  text-shadow: 0px 0px 5px rgba(66, 66, 66, .75);
}

.InputStyle:focus {
  outline: none;
}