.card{
  background-color: #f5f1ed;
  width: 800px;
  height: 300px;
  display: flex;
  justify-content: space-between;
  border: 4px inset #1C6EA4;
      border-radius: 9px 27px 18px 11px;
  }
  
  .cardImg{
      margin-left: 2%;
      align-self: center;
      width: 200px;
  }
  
  .cardInfo{
      /* background-color: red; */
      -webkit-box-shadow: 19px 11px 26px 10px rgba(0, 0, 0, 0.78);
      box-shadow: 19px 11px 26px 10px rgba(0, 0, 0, 0.78);
      display: flex;
      font-family: "Comic Sans MS", cursive, sans-serif;
      letter-spacing: 1.8px;
      word-spacing: 1.6px;
      color: #4A3401;
      /* text-decoration: none; */
      font-weight: normal;
      font-style: normal;
      font-variant: normal;
      text-transform: none;
      background-color: #a99985;
      border-radius: 0px 40px 40px 0px;
      flex-direction: column;
      justify-content: space-around;
      width: 550px;
  }
  
  .titleStyle{
      background-color: #f5f1ed;
      /* font-family: "Montserrat", sans-serif */
      text-decoration: none;
      justify-content: space-around;
          color: #252323;
          font-size: 0.9em;
          text-transform: uppercase;
          font-weight: 500;
      
      
  }
  
  .titleStyle > a {
      justify-content: space-around;
          color: #252323;
          text-decoration: none;
          font-size: 1.0em;
          text-transform: uppercase;
          font-weight: 450;
          /* display: inline-block; */
          width: 80px;
          -webkit-transition: all 0.2s ease-in-out;
          transition: all 0.2s ease-in-out;
  }