.bck{
  /* background-color: brown; */
  background-size:cover;
  height: 1300px;
}

.contenedor{
  margin-top: 5%;
  /* background-color: yellow; */
  height: 50%;
  display: flex ;
  justify-content: space-around;
  

}
.moodDiv{
   flex-direction: column;
   justify-content: space-around;
   align-items: space-around;
}

.numInput{
      padding: 5px;
          font-size: 25px;
          border-width: 2px;
          border-color: #ffe708;
          background-color: #f1eeee;
          color: #000000;
          border-style: outset;
          border-radius: 9px;
          box-shadow: 0px 0px 5px rgba(66, 66, 66, .75);
          text-shadow: 0px 0px 5px rgba(66, 66, 66, .75);
}


.formStyle{
  background-color: #dad2bc;
  width: 900px;
  height: 800px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: flex-start;
  font-size: 25px;
}

.moodStyle{
  flex-wrap: wrap;
  appearance: none;
      /* Additional resets for further consistency */
      background-color: #ffffff;
      border: none;
      border-radius: 4%;
      padding: 0 4em 0 0;
      /* width: 100%; */
      font-family: inherit;
      font-size: 25px;
      cursor: inherit;
      line-height: inherit;
}

.errorStyle{
  background-color: #dad2bc;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  margin-top: 2%;
  width: 800px;
  height: 600px;
}
.errorStyle > h1 {
  justify-self: flex-start ;
}